import React, { PureComponent, Suspense } from "react";
import Masonry from 'react-masonry-component';
import { isTagbox } from '../../../actions/api'
import './scss/NewsCard.scss'
const Card = React.lazy(() => import('./Elements/Card'));
class NewsCard extends PureComponent {

  render() {
    const { postData, completeDataObject, adjustWidth, CardStyle, ThemeStyle, ThemeInfo,  clickToShowPopUp, onClickToCTA, onClickPopUpSlider } = this.props;
    let counter = 0;

    return (
      <Masonry
        className={`tb_nc_post_container`}
        tabIndex='0'
        aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed"
        elementType={'div'}
        options={{
          transitionDuration: 0,
          percentPosition: true,
          itemSelector: '.tb_nc_post_wrapper',
          horizontalOrder: true,
          resize: true,
        }}
        disableImagesLoaded={false}
        updateOnEachImageLoad={false}
        imagesLoadedOptions={{}}
        enableResizableChildren={true}>{
          ((postData && postData.length > 0)) && postData.map((item, index) => {
            const cardData = completeDataObject[item];
            counter = (isTagbox && ThemeInfo?.id == 83) ? index % 6 === 0 ? 1 : counter + 1 : 1;
            return <Card key={index} adjustWidth={adjustWidth} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} onClickPopUpSlider={onClickPopUpSlider} Post={cardData} index={index} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo}/>
          })
        }
      </Masonry>
    );
  }
}

export default NewsCard


